import React from 'react';
import GoogleLogo from './icon/GoogleLogo';

type Props = {
    title: string;
    onClick: () => void;
};

const GoogleButton: React.FC<Props> = ({ title, onClick: handleClick }) => {
    return (
        <button
            className="flex items-center gap-2 p-2 text-lg transition-shadow bg-white border rounded shadow-sm w-max hover:shadow-md"
            onClick={handleClick}
            type="button"
        >
            <GoogleLogo />
            {title} with Google
        </button>
    );
};

export default GoogleButton;
